class Reviews {
  constructor() {
    this.reviews = []
  }
  addReview({
    authorLastName,
    createdDate,
    authorFirstName,
    rating,
    id,
    text,
    title
  }) {}
}

const Review = function({
  authorLastName,
  createdDate,
  authorFirstName,
  rating,
  id,
  text,
  title
}) {
  this._authorLastName = authorLastName
  this._createdDate = createdDate
  this._rating = rating
  this._id = id
  this._text = text
  this._title = title
  this._authorFirstName = authorFirstName
}
Review.prototype.isValid = function() {
  return !!(
    this._authorFirstName &&
    this._authorLastName &&
    this._rating &&
    this._id &&
    this._text &&
    this._title
  )
}
Review.prototype.name = function() {
  return `${this._authorFirstName} ${this._authorLastName}`
}
Review.prototype.rating = function() {
  return Math.round(this._rating)
}
Review.prototype.id = function() {
  return this._id
}
Review.prototype.review = function() {
  return {
    title: this._title,
    text: this._text
  }
}

export default Review
